
.gallery-section {
  position: relative;
  overflow: hidden;
  &::before {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 210px;
    background-color: var(--dark-900);
  }
}

.gallery-item {
  position: relative;
  &:hover {
    .thumb {
      img {
        @include transform(scale(1.1, 1.1));
      }
    }
    .content {
      bottom: rem(30px);
      opacity: 1;
    }
  }
  .thumb {
    height: rem(545px);
    overflow: hidden;
    @include border-radius(20px);
    @include media(1399px) {
      height: rem(445px);
    }
    @include media(1199px) {
      height: rem(354px);
    }
    @include media(991px) {
      height: rem(250px);
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
      @include transition(all 0.5s);
    }
  }
  .content {
    position: absolute;
    bottom: rem(10px);
    left: rem(30px);
    width: calc(100% - rem(60px));
    padding: rem(30px);
    background-color: #fff;
    @include border-radius(20px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: rem(15px);
    opacity: 0;
    @include transition(all 0.3s);
    @include media(1199px) {
      left: rem(15px);
      width: calc(100% - rem(30px));
      padding: rem(20px);
    }
    .gallery-item-btn {
      width: rem(50px);
      height: rem(50px);
      background-color: var(--primary);
      color: #fff;
      @include border-radius(50%);
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  &.style-md {
    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 70%;
      background: linear-gradient(to top, #0B0D26, rgba(#D9D9D9, 0));
      @include border-radius(20px);
      z-index: 1;
      opacity: 0;
      @include transition(all 0.3s);
    }
    &:hover {
      &::before {
        opacity: 1;
      }
    }
    .thumb {
      height: rem(380px);
      @include media(1399px) {
        height: rem(310px);
      } 
      @include media(1399px) {
        height: rem(280px);
      }
      @include media(767px) {
        height: rem(210px);
      }
    }
    .content {
      z-index: 1;
      background-color: transparent;
      padding: 0;
      p {
        color: #fff;
      }
      h3 {
        a {
          color: #fff;
          &:hover {
            color: var(--primary);
          }
        }
      }
      .gallery-item-btn {
        width: rem(40px);
        height: rem(40px);
        font-size: rem(14px);
      }
    }
  }
}

#galleryTabContent {
  .tab-pane {
    &.show {
      .row {
        div[class*="col-"] {
          opacity: 1;
          transform: translateY(0);
        }
      }
    }
    .row {
      div[class*="col-"] {
        opacity: 0;
        transform: translateY(50px);
        transition: all 0.5s;
        &:nth-child(1) {
          transition-delay: .1s;
        }
        &:nth-child(2) {
          transition-delay: .2s;
        }
        &:nth-child(3) {
          transition-delay: .3s;
        }
        &:nth-child(4) {
          transition-delay: .4s;
        }
        &:nth-child(5) {
          transition-delay: .5s;
        }
        &:nth-child(6) {
          transition-delay: .6s;
        }
        &:nth-child(7) {
          transition-delay: .7s;
        }
        &:nth-child(8) {
          transition-delay: .8s;
        }
        &:nth-child(9) {
          transition-delay: .9s;
        }
        &:nth-child(10) {
          transition-delay: .10s;
        }
      }
    }
  }
}

.gallery-slider {
  .slick-list {
    margin: 0 rem(-15px);
    @include media(991px) {
      margin: 0 rem(-10px);
    }
  }
  .gallery-slide {
    padding: 0 rem(15px);
    @include media(991px) {
      padding: 0 rem(10px);
    }
    &.slick-active:not(.slick-current) {
      .gallery {
        .thumb {
          @include border-radius(0 20px 20px 0)
        }
      }
    }
    &.slick-current ~ .slick-active {
      .gallery {
        .thumb {
          @include border-radius(20px 0 0 20px)
        }
      }
    }
  }
}