
.form-control {
  padding: rem(16px) rem(20px);
  border: 1px solid var(--border-color);
  width: 100%;
  background-color: transparent;
  @include border-radius(30px);
  height: rem(58px);
  @include placeholder(var(--dark-800));
  &:focus {
    background-color: #fff;
    border-color: var(--primary) !important;
    box-shadow: 0 0 5px rgba(var(--primary), 0.35);
    color: #000;
  }
  &[readonly] {
    background-color: #fafaf7;
  }
  &.form-control-md {
    height: 45px;
  }
  &.form-control-sm {
    height: 35px;
  }
}

.form-select {
  padding: rem(16px) rem(20px);
  width: 100%;
  border: 1px solid form;
  cursor: pointer;
  background-color: transparent;
  height: rem(58px);
  @include border-radius(30px);
  color: var(--dark-800);
  &:focus {
    background-color: #fff;
    border-color: var(--primary);
    box-shadow: none;
  }
  &.select-sm {
    height: rem(35px);
    font-size: rem(14px);
    padding: rem(5px);
  }
}

textarea {
  min-height: rem(130px) !important;
  resize: none;
  width: 100%;
}

label {
  color: #000;
  margin-bottom: rem(10px);
  font-size: rem(15px);
  font-weight: 500;
}
.input-group > .form-control,
.input-group > .select {
  position: relative;
  flex: 1 1 auto;
  width: 1%;
  min-width: 0;
}
.input-group {
  select {
    background-color: transparent;
    border: none;
  }
}
.custom-radio {
  position: relative;
  padding-left: 0;
  input[type=radio] {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    cursor: pointer;
    &:checked {
      ~ label {
        &::before {
          border-width: 2px;
          border-color: $base-color;
        }
        &::after {
          opacity: 1;
        }
      }
    }
  }
  label {
    margin-bottom: 0;
    position: relative;
    padding-left: 20px;
    font-size: rem(14px);
    font-weight: 400;
    &::before {
      position: absolute;
      content: '';
      top: 4px;
      left: 0;
      width: 15px;
      height: 15px;
      border: 1px solid #888888;
      @include border-radius(50%);
      @include transition(all 0.3s);
    }
    &::after {
      position: absolute;
      content: '';
      top: 8px;
      left: 4px;
      width: 7px;
      height: 7px;
      background-color: $base-color;
      @include border-radius(50%);
      opacity: 0;
      @include transition(all 0.3s);
    }
  }
}
.custom-checkbox {
  padding-left: rem(25px);
  input {
    display: none;
    &:checked {
      ~ label {
        &::before {
          content: "\f14a";
          color: $base-color;
        }
      }
    }
  }
  label {
    position: relative;
    font-size: rem(15px);
    font-weight: 400;
    cursor: pointer;
    margin-bottom: 0;
    &::before {
      position: absolute;
      content: "\f04d";
      font-family: 'Line Awesome Free';
      font-weight: 900;
      top: 3px;
      left: rem(-25px);
      font-size: rem(20px);
      line-height: 1;
      @extend %transition;
    }
  }
}
