.project-one {
  & + & {
    margin-top: rem(30px);
  }
  display: flex;
  flex-wrap: wrap;
  align-items: center; 
  gap: rem(40px);
  background-color: #fff;
  @include border-radius(20px);
  padding: rem(20px) rem(40px);
  box-shadow: 0 0 60px rgba(#000, 0.05);
  @include media(1399px) {
    gap: rem(30px);
    padding: rem(20px) rem(30px);
  }
  @include media(767px) {
    justify-content: center;
    text-align: center;
    gap: rem(15px);
  }
  @include media(575px) {
    padding: rem(20px);
  }
  &:hover {
    .thumb {
      opacity: 1;
    }
    .icon {
      transform: rotate(-45deg);
    }
  }

  .number {
    width: 65px;
    height: 65px;
    background-color: var(--light-100);
    @include border-radius(10px);
    font-size: rem(30px);
    font-weight: 700;
    color: var(--primary);
    display: inline-flex;
    justify-content: center;
    align-items: center;
  }

  .title {
    font-size: rem(30px);
    font-weight: 700;
    width: 20ch;
    @include media(1399px) {
      font-size: rem(24px);
    }
    @include media(1199px) {
      width: 40ch;
    }
    @include media(991px) {
      width: 35ch;
      font-size: rem(20px);
    }
  }

  .description {
    max-width: 40ch;
    @include media(991px) {
      max-width: 35ch;
    }
  }

  .thumb {
    width: 212px;
    height: 105px;
    @include border-radius(20px);
    overflow: hidden;
    margin: 0 auto;
    opacity: 0;
    @include transition(all 0.3s);
    @include media(1199px) {
      opacity: 1;
      margin-inline-start: 0;
    }
  }
  .icon {
    margin-inline-start: auto;
    @include transition(all 0.3s);
    @include media(767px) {
      margin-inline-start: 0;
    }
  }
}