.message-section-one {
  position: relative;
  z-index: 1;
  .message-section-el {
    position: absolute;
    z-index: -1;
    &.message-left-el {
      top: 0;
      left: 0;
    }

    &.message-right-el {
      bottom: 0;
      right: 0;
    }
  }
}

.overview-item {
  padding: rem(30px) rem(40px);
  background-color: #fff;
  @include border-radius(20px);
  box-shadow: 0 0 60px rgba(#000, 0.05);
  height: 100%;
  @include media(1199px){
    padding: rem(30px);
  }
  @include media(575px){
    padding: rem(20px);
  }
  .icon {
    height: 60px;
    @include media(1399px) {
      height: 50px;
    }
    @include media(575px) {
      height: 40px;
    }
  }
  .amount {
    font-family: var(--display-font);
    font-size: rem(42px);
    margin-top: rem(30px);
    margin-bottom: rem(15px);
    @include media(1399px) {
      font-size: rem(36px);
    }
    @include media(991px) {
      font-size: rem(32px);
    }
    @include media(575px) {
      font-size: rem(26px);
      margin-top: rem(20px);
      margin-bottom: rem(10px);
    }
  }
  p {
    @include media(575px) {
      font-size: rem(14px);
    }
  }
}

.message-wrapper {
  padding-inline-start: rem(50px);
  @include media(991px) {
    padding-inline-start: 0;
  }
  .message-form {
    margin-top: rem(40px);
  }
}


.message-section-two-inner {
  padding: rem(80px) 0;
  position: relative;
  z-index: 1;
  @include media(1199px) {
    padding: rem(50px) 0;
  }
  @include media(991px) {
    padding: 0;
  }
  .message-section-two-thumb {
    position: absolute;
    top: 0;
    inset-inline-start: 0;
    width: 64%;
    height: 100%;
    z-index: -1;
    @include border-radius(10px);
    overflow: hidden;
    @include media(991px) {
      position: static;
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
}

.message-wrapper-two {
  padding: rem(60px);
  background-color: #fff;
  @include border-radius(20px);
  box-shadow: 0 -5px 20px -1px rgba(#131022, 0.05);
  @include media(1399px) {
    padding: rem(40px);
  }
  @include media(575px) {
    padding: rem(30px);
  }
  .section-title {
    margin-bottom: rem(20px);
  }
  .message-form {
    margin-top: rem(40px);
  }
}

.message-three-thumb {
  width: 512px;
  height: 512px;
  position: relative;
  z-index: 1;
  @include media(1199px) {
    width: 450px;
    height: 450px;
  }
  @include media(575px) {
    width: 280px;
    height: 280px;
  }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: -10px;
    width: 96%;
    height: 96%;
    background-color: var(--primary);
    @include border-radius(50%);
    z-index: -1;
    @include media(575px) {
      left: -5px;
    }
  }
  img {
    width: 512px;
    height: 512px;
    @include object-fit;
    @include border-radius(50%);
    @include media(1199px) {
      width: 450px;
      height: 450px;
    }
    @include media(575px) {
      width: 280px;
      height: 280px;
    }
  }
  .message-three-video-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: rem(70px);
    height: rem(70px);
    background-color: #fff;
    @include border-radius(50%);
    color: var(--primary);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(22px);
    @include media(575px) {
      width: rem(45px);
      height: rem(45px);
      font-size: rem(14px);
    }
  }
  &-content {
    position: absolute;
    bottom: 15px;
    inset-inline-end: 0;
    width: 130px;
    height: 130px;
    background-color: #fff;
    @include border-radius(50%);
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    gap: 0;
    &::before {
      position: absolute;
      content: '';
      top: 10px;
      left: 10px;
      width: calc(100% - 20px);
      height: calc(100% - 20px);
      @include border-radius(50%);
      border: 1px dashed var(--light-200);
    }
    span {
      font-size: rem(12px);
    }
    p {
      font-size: rem(20px);
      font-weight: 700;
      display: block;
      color: #fff;
      -webkit-text-fill-color: transparent;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: var(--primary);
    }
  }
}