.inner-banner {
  padding-top: rem(300px);
  padding-bottom: rem(150px);
  position: relative;
  z-index: 1;
  @include media(1399px) {
    padding-top: rem(230px);
    padding-bottom: rem(100px);
  }
  @include media(1199px) {
    padding-top: rem(180px);
  }
  @include media(575px) {
    padding-top: rem(120px);
    padding-bottom: rem(60px);
  }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--dark-900);
    opacity: 0.8;
    z-index: -1;
  }
  &-bg-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include object-fit;
    z-index: -2;
  }
  .title {
    font-family: var(--display-font);
    color: #fff;
    font-size: rem(42px);
    font-weight: 700;
    @media (max-width: 1399px) {
      font-size: rem(36px);
    }
    @media (max-width: 1199px) {
      font-size: rem(32px);
    }
    @media (max-width: 575px) {
      font-size: rem(28px);
    }
  }
}

.page-breadcrumb {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: rem(20px);
  @include media(575px) {
    margin-top: rem(10px);
  }
  li {
    color: #fff;
    font-weight: 500;
    font-size: rem(18px);
    @include media(575px) {
      font-size: rem(16px);
    }
    &:last-child {
      &::after {
        display: none;
      }
    }
    &::after {
      content: "\f105";
      font-family: "Font Awesome 6 Free";
      font-weight: 900;
      font-size: rem(14px);
      margin: 0 rem(15px);
    }
    a {
      color: #fff;
    }
  }
}