.brand-section {
  overflow: hidden;
}

.brand-item {
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    display: inline-block;
  }
}

.brand-slider {
  .slick-list {
    margin: 0 rem(-15px);
  }
  .brand-slide {
    padding: 0 rem(15px);
  }
}