.service-details-sidebar {
  padding: rem(40px);
  background-color: var(--light-100);
  @include border-radius(5px);
  @include media(575px) {
    padding: rem(25px);
  }
  &-box {
    padding: rem(25px) rem(30px) rem(30px) rem(30px);
    background-color: #fff;
    @include border-radius(5px);
    margin-top: rem(30px);
  }
}