
button {
  &:focus {
    outline: none;
  }
}

.btn {
  padding: rem(17px) rem(30px);
  font-weight: 500;
  text-transform: uppercase;
  @include media(1399px) {
    padding: rem(15px) rem(30px);
  }
  @include media(1199px) {
    padding: rem(12px) rem(30px);
  }
  @include media(575px) {
    padding: rem(10px) rem(25px);
  }
  &:focus {
    box-shadow: none;
    outline: none;
  }
}

.btn-primary {
  background-color: var(--primary);
  border-color: var(--primary);
  &:hover,
  &:focus {
    border-color: var(--primary);
    background-color: var(--primary);
  }
}

.btn-outline-primary {
  color: var(--dark-900);
  border-color: var(--primary);
  &:hover,
  &:focus {
    background-color: var(--primary);
    border-color: var(--primary);
  }
}

.btn-outline-secondary-primary {
  color: var(--dark-900);
  border-color: var(--border-color);
  &:hover,
  &:focus {
    background-color: var(--primary);
    border-color: var(--primary);
    color: #fff;
  }
}

[class*="btn"] {
  &.btn-md {
    padding: rem(10px) rem(20px);
    font-size: rem(15px);
  }
  &.btn-sm {
    padding: rem(6px) rem(10px);
    font-size: rem(14px);
  }
}
