

.pricing-switcher {
  width: rem(130px);
  display: flex;
  flex-flow: column;
  gap: rem(10px);
  .pricing-switcher-btn {
    @include border-radius(99px);
    border: 1px solid var(--border-color);
    text-transform: capitalize;
    &.active {
      background-color: var(--primary);
      border-color: var(--primary);
      color: #fff;
    }
  }
}

.pricing-card {
  height: 100%;
  @include border-radius(20px);
  overflow: hidden;
  position: relative;
  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%;
    background: linear-gradient(to top, #010D14, rgba(#010D14, 0));
  }
  img {
    width: 100%;
    height: 100%;
    @include object-fit;
  }
  &-overlay-content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: rem(40px);
    z-index: 1;
    @include media(575px) {
      padding: rem(20px);
    }
    .top {
      display: flex;
      align-items: center;
      margin-bottom: rem(20px);
      i {
        color: #fff;
        font-size: rem(66px);
        width: rem(60px);
        @include media(575px) {
          font-size: rem(48px);
          width: rem(42px);
        }
      }
      .title {
        font-size: rem(24px);
        color: #fff;
        font-weight: 700;
        width: calc(100% - rem(60px));
        padding-left: rem(15px);
        max-width: 15ch;
        @include media(575px) {
          width: calc(100% - rem(42px));
          font-size: rem(18px);
        }
      }
    }
    .second-title {
      @include media(575px) {
        font-size: rem(16px);
      }
    }
  }

  .pricing-card-social {
    display: flex;
    align-items: center;
    gap: rem(10px);
    margin-top: rem(30px);
    @include media(575px) {
      margin-top: rem(20px);
    }
    li {
      a {
        width: 26px;
        height: 26px;
        border: 1px solid var(--light-200);
        color: #fff;
        @include border-radius(50%);
        font-size: rem(12px);
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

.pricing-one {
  @include border-radius(20px);
  border-top: 5px solid var(--primary);
  background-color: #fff;
  box-shadow: 0 0 60px rgba(#000, 0.05);
  display: flex;
  flex-flow: column;
  &-header {
    padding: rem(40px) rem(30px);
    text-align: center;
    position: relative;
    background-color: #fff2e6;
    @include border-radius(20px 20px 0 0);
    @include media(575px) {
      padding: rem(20px) rem(25px);
    }
    .title {
      font-weight: 700;
      font-size: rem(30px);
      @include media(575px) {
        font-size: rem(24px);
      }
    }
  }
  &-body {
    padding: rem(20px) rem(40px) rem(30px) rem(40px);
    @include media(575px) {
      padding: rem(25px);
    }
    .price {
      font-size: rem(42px);
      font-weight: 700;
      color: var(--dark-900);
      font-family: var(--display-font);
      margin-bottom: rem(10px);
      @include media(575px) {
        font-size: rem(32px);
      }
      sub {
        color: var(--dark-800);
        font-weight: 400;
        font-size: rem(16px);
        font-family: var(--body-font);
      }
      .yearly-price {
        display: none;
      }
    }
  }
  .pricing-feature-list {
    margin-top: rem(40px);
    li + li {
      margin-top: rem(10px);
    }
    li {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: rem(10px);
      font-size: rem(18px);
      @include media(575px) {
        font-size: rem(16px);
      }
    }
  }
  &-footer {
    padding: rem(20px) rem(40px) rem(30px) rem(40px);
    margin-top: auto;
    @include media(575px) {
      padding: rem(20px) rem(25px) rem(25px) rem(25px);
    }
  }
}

.pricing-section-two {
  .pricing-switcher {
    flex-flow: row;
    .btn {
      padding-block: rem(14px);
      padding-inline: rem(35px);
    }
  }
}

.pricing-two {
  padding: rem(30px) rem(30px) rem(40px) rem(30px);
  border: 1px solid var(--border-color);
  @include border-radius(20px);
  text-align: center;
  height: 100%;
  .title {
    margin-bottom: rem(30px);
  }
  .price {
    font-size: rem(30px);
    font-weight: 700;
    color: var(--dark-900);
    margin-bottom: rem(10px);
    display: inline-block;
    padding: rem(5px) rem(30px);
    background-color: var(--light-100);
    @include border-radius(5px);
    @include media(575px) {
      font-size: rem(24px);
    }
    sub {
      color: var(--dark-800);
      font-weight: 400;
      font-size: rem(16px);
      font-family: var(--body-font);
    }
    .yearly-price {
      display: none;
    }
  }
  &-feature-list {
    margin-bottom: rem(20px);
    margin-top: rem(10px);
    li + li {
      border-top: 1px solid var(--border-color);
    }
    li {
      padding: rem(12px) 0;
      span {
        color: var(--dark-800);
        display: none;
        @include media(1199px) {
          display: inline-block;
        }
      }
      i {
        color: var(--dark-900);
        @include media(1199px) {
          margin-inline-end: rem(5px);
        }
      }
    }
  }
  &.pricing-two-main {
    text-align: left;
    padding-top: rem(96px);
    .price {
      padding: 0;
      background-color: transparent;
    }
    .pricing-two-feature-list {
      li {
        span {
          display: inline-block;
        }
      }
    }
  }
}