@import './_reset';

@import './_color';
@import './_bg';

.section-top {
  padding-bottom: rem(60px);
  @include media(1399px) {
    padding-bottom: rem(50px);
  }
  @include media(991px) {
    padding-bottom: rem(40px);
  }
}

.section-top-title-two {
  font-size: rem(16px);
  color: var(--primary);
  font-weight: 500;
  text-transform: uppercase;
  margin-bottom: rem(10px);
}

.section-title {
  font-family: var(--display-font);
  font-size: rem(42px);
  font-weight: 700;
  @include media(1399px) {
    font-size: rem(36px);
  }
  @include media(991px) {
    font-size: rem(32px);
  }
  @include media(575px) {
    font-size: rem(26px);
  }
}

.rounded-sm {
  @include border-radius(5px);
}