.news-letter-wrapper {
  position: relative;
  overflow: hidden;
  padding: rem(60px);
  @include border-radius(20px);
  z-index: 1;
  @include media(991px) {
    padding: rem(40px) rem(20px);
  }
  &::before {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--dark-900);
    opacity: 0.8;
  }
  &-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include object-fit;
    z-index: -1;
  }
  &-inner {
    width: 505px;
    position: relative;
    z-index: 1;
    @include media(991px) {
      width: 100%;
    }
  }
  .section-title {
    color: #fff;
    @include media(991px) {
      text-align: center;
    }
  }

  .subscribe-form {
    margin-top: rem(35px);
  }

  .news-letter-icon-wrapper {
    position: absolute;
    top: 0;
    inset-inline-end: rem(60px);
    height: 100%;
    width: 330px;
    background-color: #fff;
    clip-path: polygon(29% 0, 100% 0, 75% 100%, 0% 100%);
    @include media(1199px) {
      inset-inline-end: rem(30px);
    }
    @include media(991px) {
      display: none;
    }
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: rem(20px);
      width: 100%;
      height: 100%;
      clip-path: polygon(29% 0, 100% 0, 75% 100%, 0% 100%);
      background-color: var(--primary);
    }
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      @include transform(translate(-50%, -50%));
      min-height: 115px;
    }
  }
}

.subscribe-form {
  position: relative;
  input {
    height: 80px;
    border: 1px solid var(--border-color);
    @include border-radius(99px);
    width: 100%;
    padding: rem(20px) rem(150px) rem(20px) rem(30px);
    color: #fff;
    @include placeholder(var(--light-200));
    @include transition(all 0.3s);
    &:focus {
      border-color: var(--secondary);
    }
  }
  button {
    position: absolute;
    top: 10px;
    inset-inline-end: 10px;
    height: calc(100% - 20px);
    padding: rem(10px) rem(30px);
    background-color: var(--secondary);
    color: #fff;
    @include border-radius(99px);
    text-transform: uppercase;
    font-weight: 500;
  }
}