.blog-details-wrapper {
  background-color: var(--light-100);
  @include border-radius(5px);
  .blog-meta {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: rem(30px);
    margin-bottom: rem(30px);
    @include media(575px) {
      gap: rem(10px);
    }
  }
  .blog-details-content {
    padding: rem(40px);
    @include media(575px) {
      padding: rem(20px);
    }
  }
  .blog-quote {
    margin-top: rem(20px);
  }
  .check-list {
    li {
      margin-top: 10px;
    }
    li + li {
      margin-top: 10px;
    }
  }
}

.blog-details-tags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: rem(10px);
  li {
    span {
      font-weight: 500;
      color: var(--dark-900);
    }
    a {
      padding: rem(4px) rem(12px);
      background-color: #fff;
      @include border-radius(5px);
    }
  }
}

.blog-quote {
  position: relative;
  padding: rem(30px) rem(20px) rem(30px) rem(90px);
  background-color: #fff;
  @include border-radius(5px);
  @include media(575px) {
    padding: rem(20px);
  }
  &::before {
    position: absolute;
    content: "\f10d";
    top: rem(30px);
    inset-inline-start: rem(30px);
    font-family: "Font Awesome 6 Free";
    font-weight: 900;
    line-height: 1;
    font-size: rem(46px);
    color: var(--primary);
    @include media(575px) {
      opacity: 0.1;
    }
  }
  p {
    font-size: rem(20px);
    @include media(575px) {
      font-size: rem(16px);
    }
  }
  &-name {
    position: relative;
    padding-inline-start: rem(80px);
    margin-top: rem(20px);
    @include media(575px) {
      padding-inline-start: rem(50px);
    }
    &::before {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      width: 70px;
      height: 2px;
      background-color: var(--primary);
      margin-top: -1px;
      @include media(575px) {
        width: 40px;
      }
    }
  }
}

.blog-prev-next {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: rem(20px);
  margin-top: rem(60px);
  padding: rem(30px);
  border: 1px solid var(--border-color);
  @include border-radius(5px);
  position: relative;
  &::after {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    width: 1px;
    height: 50px;
    background-color: var(--primary);
    margin-top: -25px;
    @include media(575px) {
      display: none;
    }
  }
}

.blog-arrow-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  @include media(575px) {
    width: 100%;
  }
  .icon {
    width: rem(60px);
    height: rem(60px);
    background-color: var(--light-100);
    color: var(--dark-900);
    font-size: rem(22px);
    @include border-radius(5px);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;
    @include media(575px) {
      width: rem(45px);
      height: rem(45px);
    }
    &:hover {
      background-color: var(--primary);
      color: #fff;
    }
  }
  .title {
    max-width: 13ch;
  }
  &.blog-next {
    .title {
      text-align: right;
    }
  }
}

.comment-wrapper {
  margin-top: rem(60px);
}

.single-comment {
  display: flex;
  flex-wrap: wrap;
  margin-top: rem(30px);
  padding: rem(30px);
  background-color: var(--light-100);
  @include media(575px) {
    padding: rem(20px);
  }
  .thumb {
    width: 65px;
    height: 65px;
    img {
      @include border-radius(5px);
    }
  }
  .content {
    width: calc(100% - 65px);
    padding-inline-start: rem(20px);
    @include media(575px) {
      width: 100%;
      padding-inline-start: 0;
      margin-top: rem(20px);
    }
  }
}

.reply-btn {
  font-size: rem(14px);
  font-weight: 700;
  color: var(--dark-900);
  text-transform: uppercase;
  padding: rem(3px) rem(12px);
  border: 1px solid var(--border-color);
  @include border-radius(5px);
  &:hover {
    background-color: #fff;
  }
}

.reply-wrapper {
  margin-top: rem(60px);
  form {
    margin-top: rem(40px);
    .form-control,
    .form-select {
      border-width: 2px;
    }
  }
}

.blog-sidebar {
  position: sticky;
  top: 100px;
}

.blog-sidebar-box {
  padding: rem(30px);
  background-color: #fff;
  @include border-radius(20px);
  box-shadow: 0 0 60px rgba(#000, 0.05);
  @include media(575px) {
    padding: rem(20px);
  }
  .title {
    font-weight: 700;
    margin-bottom: rem(40px);
  }
}

.blog-sidebar-search {
  position: relative;
  input {
    height: 56px;
    border: 1px solid var(--border-color);
    @include border-radius(20px);
    padding: rem(10px) rem(20px);
    width: 100%;
  }
  button {
    position: absolute;
    top: 50%;
    inset-inline-end: rem(20px);
    @include transform(translateY(-50%));
    color: var(--dark-900);
  }
}

.short-blog-list {
  .short-blog + .short-blog {
    margin-top: rem(20px);
  }
}

.short-blog {
  display: flex;
  flex-wrap: wrap;
  .thumb {
    width: 80px;
    height: 85px;
    display: block;
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
      @include border-radius(5px);
    }
  }
  .content {
    width: calc(100% - 80px);
    padding-inline-start: rem(15px);
  }
  .post-date {
    font-size: rem(14px);
    margin-bottom: rem(8px);
    i {
      color: var(--primary);
      margin-inline-end: rem(5px);
    }
  }
}

.blog-category-list {
  padding: rem(10px) rem(20px);
  @include border-radius(5px);
  background-color: var(--light-100);
  li {
    position: relative;
    padding-inline-start: rem(30px);
    padding-block: rem(10px);
    &::before {
      position: absolute;
      content: "\f07c";
      top: 10px;
      left: 0;
      font-family: "Font Awesome 6 Free";
      font-weight: 400;
      color: var(--primary);
    }
    a {
      display: flex;
      flex-wrap: wrap;
      gap: 5px;
      align-items: center;
      justify-content: space-between;
      color: var(--dark-900);
      transition: all 0.3s;
      &:hover {
        color: var(--primary);
      }
    }
  }
}

.blog-tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  li {
    a {
      padding: rem(5px) rem(15px);
      background-color: var(--light-100);
      @include border-radius(5px);
      transition: all 0.3s;
      &:hover {
        background-color: var(--primary);
        color: #fff;
      }
    }
  }
}