.contact-item {
  padding: rem(30px);
  background-color: var(--light-100);
  height: 100%;
  .icon {
    width: rem(40px);
    height: rem(40px);
    background-color: #fff;
    color: var(--primary);
    @include border-radius(5px);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(20px);
  }
  .content {
    margin-top: rem(20px);
    p {
      margin-top: rem(15px);
    }
    a {
      word-break: break-all;
    }
  }
}

.contact-map {
  filter: grayscale(1);
  iframe {
    width: 100%;
    height: 650px;
    border: none;
    @include media(1399px) {
      height: 550px;
    }
    @include media(991px) {
      height: 450px;
    }
    @include media(575px) {
      height: 300px;
    }
  }
}