.service-one {
  &:hover {
    .thumb {
      img {
        transform: scale(1.1, 1.1);
      }
    }
    .icon {
      background-color: var(--primary);
    }
  }
  .thumb {
    height: rem(345px);
    overflow: hidden;
    @include border-radius(20px);
    @include media(1399px) {
      height: rem(300px);
    }
    @include media(1199px) {
      height: rem(250px);
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
      @include transition(all 0.5s);
    }
  }
  .content {
    margin-left: 30px;
    margin-right: 30px;
    margin-top: rem(-40px);
    padding: rem(40px) rem(30px);
    box-shadow: 0 0 10px rgba(#000, 0.1);
    @include border-radius(20px);
    text-align: center;
    position: relative;
    background-color: #fff;
    @include media(1199px) {
      margin-left: 15px;
      margin-right: 15px;
      padding: rem(30px) rem(20px);
    }
  }
  .icon {
    width: 65px;
    height: 65px;
    position: relative;
    background-color: var(--light-100);
    @include border-radius(20px);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @include transition(all 0.5s);
    img {
      max-height: 40px;
    }
  }
  .title {
    margin-top: rem(15px);
    margin-bottom: rem(10px);
  }
}

.service-two {
  background-color: #fff;
  @include border-radius(20px);
  box-shadow: 0 0 60px rgba(#000, 0.05);
  &:hover {
    .content {
      .icon {
        background-color: var(--primary);
      }
    }
    .service-two-btn {
      i {
        color: var(--primary);
      }
    }
  }
  .thumb {
    height: rem(328px);
    overflow: hidden;
    @include border-radius(20px 20px 0 0);
    @include media(1399px) {
      height: rem(280px);
    }
    @include media(1199px) {
      height: rem(250px);
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
  .content {
    padding: 0 rem(40px) rem(40px) rem(40px);
    position: relative;
    z-index: 1;
    @include media(1199px) {
      padding: 0 rem(30px) rem(30px) rem(30px);
    }
    .icon {
      width: rem(80px);
      height: rem(80px);
      @include border-radius(20px);
      background-color: var(--light-100);
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: rem(-40px);
      margin-bottom: rem(20px);
      @include transition(all 0.3s);
    }
  }
  .title {
    margin-bottom: rem(20px);
    @include media(1199px) {
      margin-bottom: rem(15px);
    }
  }
  .service-two-btn {
    font-weight: 500;
    display: inline-flex;
    align-items: center;
    gap: rem(8px);
    margin-top: rem(30px);
    @include media(1199px) {
      margin-top: rem(20px);
    }
  }
}

.service-section-three {
  margin-top: -90px;
  position: relative;
  z-index: 1;
}

.service-three {
  padding: rem(40px) rem(40px) 0 rem(40px);
  background-color: #fff;
  @include border-radius(5px);
  box-shadow: 0 0 60px rgba(#000, 0.05);
  margin-bottom: rem(30px);
  @include media(991px) {
    text-align: center;
  }
  @include media(575px) {
    padding: rem(30px) rem(30px) 0 rem(30px);
  }
  .icon {
    width: rem(80px);
    height: rem(80px);
    border: 1px solid var(--border-color);
    @include border-radius(50%);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-bottom: rem(20px);
  }
  p {
    margin-top: rem(20px);
  }
  &-icon-btn {
    width: rem(60px);
    height: rem(60px);
    background-color: var(--primary);
    color: #fff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: rem(20px);
    @include border-radius(50%);
    @include transition(all 0.3s);
    transform: translateY(rem(30px));
    &:hover {
      box-shadow: 0 5px 15px var(--primary);
      color: #fff;
    }
  }
}

.service-four {
  padding: rem(40px);
  background-color: #fff;
  @include border-radius(5px);
  box-shadow: 0 0 60px rgba(#000, 0.05);
  border: 1px solid transparent;
  @include transition(all 0.3s);
  height: 100%;
  display: flex;
  flex-flow: column;
  @include media(1199px) {
    padding: rem(25px);
  }
  @include media(767px) {
    align-items: center;
    text-align: center;
  }
  &:hover {
    border-color: var(--primary);
  }
  .icon {
    width: 80px;
    height: 80px;
    background-color: var(--light-100);
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius(5px);
    margin-bottom: rem(20px);
    img {
      height: 45px;
    }
  }
  p {
    margin-top: rem(20px);
    margin-bottom: rem(30px);
  }
  &-icon-btn {
    font-weight: 500;
    color: var(--dark-900);
    display: inline-flex;
    align-items: center;
    margin-top: auto;
    gap: 8px;
    i {
      color: var(--primary);
    }
  }
}