.check-list {
  li + li {
    margin-top: rem(20px);
  }
  li {
    position: relative;
    padding-left: rem(30px);
    &::before {
      position: absolute;
      top: rem(5px);
      left: 0;
      font-family: "Font Awesome 6 Free";
      font-weight: 900;
      content: "\f058";
      font-size: rem(16px);
      color: var(--primary);
      line-height: 1;
    }
  }
}

.arrow-list {
  li + li {
    margin-top: rem(20px);
  }
  li {
    position: relative;
    padding-left: rem(30px);
    &::before {
      position: absolute;
      top: rem(5px);
      left: 0;
      font-family: "Font Awesome 6 Free";
      font-weight: 900;
      content: "\f101";
      font-size: rem(16px);
      color: var(--primary);
      line-height: 1;
    }
  }
}

.line-dot-list {
  position: relative;
  margin-top: rem(-10px);
  li {
    position: relative;
    padding-left: rem(30px);
    margin-block: rem(10px);
    &:last-child {
      &::after {
        display: none;
      }
    }
    &::before {
      position: absolute;
      content: '';
      top: 11px;
      left: 0;
      width: 6px;
      height: 6px;
      background-color: var(--primary);
      @include border-radius(50%);
    }
    &::after {
      position: absolute;
      content: '';
      top: 11px;
      left: 3px;
      width: 1px;
      height: 145%;
      background-color: var(--border-color);
      z-index: -1;
    }
  }
}