.banner-section {
  padding-top: rem(300px);
  padding-bottom: rem(175px);
  background-color: var(--dark-900);
  position: relative;
  overflow: hidden;
  z-index: 1;
  @include media(1750px) {
    padding-top: rem(250px);
    padding-bottom: rem(120px);
  }
  @include media(992px) {
    padding-top: rem(150px);
    padding-bottom: rem(70px);
  }
  @include media(991px) {
    padding-bottom: rem(0);
  }
  @include media(575px) {
    padding-top: rem(120px);
  }
  .banner-top-title {
    color: #fff;
    text-transform: uppercase;
  }
  .banner-title {
    color: #fff;
    font-size: 80px;
    font-family: var(--display-font);
    max-width: 15ch;
    font-weight: 700;
    @include media(1750px) {
      font-size: rem(66px);
    }
    @include media(1399px) {
      font-size: rem(54px);
    }
    @include media(1199px) {
      font-size: rem(48px);
    }
    @include media(991px) {
      margin-left: auto;
      margin-right: auto;
    }
    @include media(575px) {
      font-size: rem(36px);
    }
  }
  .banner-description {
    color: #fff;
    max-width: 65ch;
    margin-top: rem(40px);
    @include media(1750px) {
      max-width: 55ch;
    }
    @include media(991px) {
      margin-left: auto;
      margin-right: auto;
      margin-top: rem(25px);
    }
  }
  .banner-btns {
    margin-top: rem(45px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 30px;
    @include media(991px) {
      justify-content: center;
      margin-top: rem(25px);
    }
  }
  .banner-left-line-el {
    position: absolute;
    inset-inline-start: 0;
    bottom: 0;
    opacity: 0.65;
    z-index: -3;
  }
  .banner-man-img {
    position: absolute;
    inset-inline-end: 145px;
    inset-block-end: 0;
    z-index: -1;
    @include media(1850px) {
      inset-inline-end: 105px;
    }
    @include media(1750px) {
      max-height: 550px;
    }
    @include media(1399px) {
      max-height: 455px;
    }
    @include media(1199px) {
      max-height: 420px;
      inset-inline-end: 0;
    }
    @include media(991px) {
      position: static;
      margin-top: rem(40px);
    }
  }
  .banner-angle-img {
    position: absolute;
    width: 695px;
    height: 101%;
    inset-inline-end: 135px;
    inset-block-end: 0;
    z-index: -2;
    clip-path: polygon(35% 0%, 100% 0%, 65% 100%, 0% 100%);
    @include media(1850px) {
      inset-inline-end: 95px;
    }
    @include media(1750px) {
      inset-inline-end: 0;
    }
    @include media(1399px) {
      width: 490px;
      inset-inline-end: 35px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      -o-object-fit: cover;
    }
    @include media(991px) {
      opacity: 0.35;
    }
  }
  .line-shape {
    position: absolute;
    top: 0;
    inset-inline-end: 630px;
    width: 325px;
    height: 100%;
    background-color: #fff;
    opacity: 0.1;
    clip-path: polygon(75% 0%, 100% 0%, 25% 100%, 0% 100%);
    z-index: -2;
    @include media(1850px) {
      inset-inline-end: 590px;
    }
    @include media(1750px) {
      inset-inline-end: 480px;
    }
    @include media(1399px) {
      inset-inline-end: 375px;
      width: 235px;
    }
  }
}

.call-btn {
  display: flex;
  align-items: center;
  gap: rem(12px);
  &:hover {
    i {
      background-color: var(--secondary);
    }
  }
  i {
    width: rem(60px);
    height: rem(60px);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    border: 1px solid var(--secondary);
    @include border-radius(50%);
    font-size: rem(20px);
    @include transition(all 0.3s);
  }
  span {
    small {
      font-size: rem(14px);
      display: block;
    }
    strong {
      font-weight: 500;
      font-size: rem(18px);
    }
  }
  &.call-btn-white {
    span {
      small,
      strong {
        color: #fff;
      }
    }
  }
}

.banner-section-two {
  position: relative;
  padding-top: rem(300px);
  padding-bottom: rem(140px);
  position: relative;
  background-color: var(--dark-900);
  z-index: 1;
  overflow: hidden;
  @include media(1399px) {
    padding-top: rem(230px);
    padding-bottom: rem(100px);
  }
  @include media(1199px) {
    padding-top: rem(170px);
  }
  @include media(575px) {
    padding-top: rem(130px);
    padding-bottom: rem(60px);
  }
  .banner-top-title {
    font-weight: 500;
    text-transform: uppercase;
    position: relative;
    color: #fff;
    @include media(575px) {
      font-size: rem(14px);
    }
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: var(--primary);
    }
  }
  .banner-title {
    font-size: rem(80px);
    font-weight: 700;
    color: #fff;
    font-family: var(--display-font);
    @include media(1750px) {
      font-size: rem(66px);
    }
    @include media(1399px) {
      font-size: rem(54px);
    }
    @include media(1199px) {
      font-size: rem(48px);
    }
    @include media(991px) {
      margin-left: auto;
      margin-right: auto;
    }
    @include media(575px) {
      font-size: rem(36px);
    }
  }
  .check-list {
    li + li {
      margin-top: rem(10px);
    }
    li {
      color: var(--light-200);
    }
  }
  .banner-btns {
    margin-top: rem(45px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 30px;
  }
  .banner-section-two-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include object-fit;
    z-index: -1;
  }
  .banner-section-two-angle-img {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
    max-width: 9vw;
  }
}

