.video-btn {
  display: flex;
  align-items: center;
  gap: rem(25px);

  &.video-btn-white {
    i {
      background-color: #fff;
    }
    span {
      color: #fff;
    }
  }
  i {
    width: rem(40px);
    height: rem(40px);
    background-color: var(--dark-900);
    @include border-radius(50%);
    color: var(--primary);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 0 10px rgba($primary, 0.15);
  }
}

.circle-contact-btn-main {
  position: relative;
  display: inline-flex;
  .circle-contact-btn-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.circle-contact-btn {
  --radius: 11vmin;
	--frame-size: calc(var(--radius) / 3);
	--d-outer: calc(var(--radius) * 2);
	--d-inner: calc(var(--d-outer) - var(--frame-size));
	--font-size: calc(var(--radius) / 10);
	position: relative;
	width: var(--d-outer);
	height: var(--d-outer);
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
  z-index: 1;
  @include media(575px) {
    --radius: 25vmin;
  }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid var(--primary);
    border-radius: 100vmax;
    z-index: -1;
  }
  .circle-contact-btn-text {
    position: absolute;
    width: 100%;
    height: 100%;
    animation: rotateText 10s linear infinite;
    span {
      position: absolute;
      left: 50%;
      font-size: 1.2em;
      transform-origin: 0 var(--radius);
      color: var(--primary);
    }
  }
}

@keyframes rotateText {
	0% {
		transform: rotate(360deg);
	}
	100% {
		transform: rotate(0deg);
	}
}