.team-section-one {
  overflow: hidden;
  padding-inline: rem(95px);
  @include media(1399px) {
    padding-inline: rem(40px);
  }
  @include media(991px) {
    padding-inline: rem(15px);
  }
}

.team-one {
  padding: rem(30px);
  background-color: #fff;
  box-shadow: 0 0 60px rgba(#000, 0.05);
  @include border-radius(20px);
  border-bottom: 2px solid #fff;
  @include transition(all 0.3s);
  @include media(991px) {
    padding: rem(20px);
  }
  &:hover {
    border-color: var(--primary);
    .thumb {
      .team-social-list {
        bottom: 20px;
        opacity: 1;
        li {
          transform: translateY(0) !important;
        }
      }
    }
  }
  .thumb {
    position: relative;
    height: 295px;
    @include border-radius(20px);
    overflow: hidden;
    @include media(1620px) {
      height: 250px;
    }
    @include media(1199px) {
      height: 210px;
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
    .team-social-list {
      position: absolute;
      bottom: 10px;
      width: 100%;
      padding: 0 rem(20px);
      opacity: 0;
      @include transition(all 0.3s);
      li {
        &:nth-child(1) {
          transform: translateY(10px);
        }
        &:nth-child(2) {
          transform: translateY(30px);
        }
        &:nth-child(3) {
          transform: translateY(50px);
        }
        &:nth-child(4) {
          transform: translateY(70px);
        }
      }
    }
  }
  .team-social-list {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: rem(10px);
    li {
      @include transition(all 0.3s);
      a {
        width: 35px;
        height: 35px;
        background-color: #fff;
        border: 1px solid var(--primary);
        @include border-radius(50%);
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--dark-900);
        @include transition(all 0.3s);
        &:hover {
          background-color: var(--primary);
          color: #fff;
        }
      }
    }
  }
  .content {
    text-align: center;
    margin-top: rem(30px);
  }
  &.style-two {
    background-color: transparent;
    border: none;
    padding: rem(30px) rem(40px) 0 rem(40px);
    box-shadow: none;
    position: relative;
    z-index: 1;
    @include media(575px) {
      padding: rem(30px) rem(30px) 0 rem(30px);
    }
    &:hover {
      &::before {
        background-color: var(--light-100);
        box-shadow: none;
      }
    }
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 60%;
      background-color: #fff;
      @include border-radius(20px);
      box-shadow: 0 15px 55px rgba(#000, 0.05);
      z-index: -1;
      @include transition(all 0.3s);
    }
    .thumb {
      height: 315px;
      @include media(1199px) {
        height: 250px;
      }
    }
    .content {
      margin-top: 0;
      margin-bottom: rem(30px);
      p {
        margin-top: rem(5px);
      }
    }
  }
  .team-social-list {
    li {
      a {
        border: none;
        color: var(--primary);
      }
    }
  }
}

.team-two {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  padding: rem(40px) rem(20px) rem(40px) 0;
  z-index: 1;
  @include media(1399px) {
    padding: rem(30px) rem(20px) rem(30px) 0;
  }
  @include media(1199px) {
    padding: rem(20px);
  }
  &:hover {
    &::before {
      border-color: var(--primary);
    }
  }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    inset-inline-end: 0;
    height: 100%;
    width: calc(100% - 80px);
    border: 1px solid var(--border-color);
    @include border-radius(20px);
    z-index: -1;
    @include transition(all 0.3s);
    @include media(1199px) {
      width: 100%;
    }
  }
  .thumb {
    width: rem(180px);
    height: rem(180px);
    overflow: hidden;
    @include border-radius(20px);
    @include media(1399px) {
      width: rem(150px);
      height: rem(150px);
    }
    @include media(1199px) {
      width: 100%;
      height: rem(200px);
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
  .content {
    width: calc(100% - rem(180px));
    padding-inline-start: rem(20px);
    @include media(1399px) {
      width: calc(100% - rem(150px));
    }
    @include media(1199px) {
      width: 100%;
      padding-inline-start: 0;
      margin-top: rem(20px);
      text-align: center;
    }
  }
  .team-social-list {
    display: inline-flex;
    flex-wrap: wrap;
    align-items: center;
    background-color: var(--light-100);
    @include border-radius(20px);
    margin-top: rem(20px);
    li {
      position: relative;
      &:last-child {
        &::after {
          display: none;
        }
      }
      &::after {
        position: absolute;
        content: '';
        top: 50%;
        right: 0;
        width: 1px;
        height: 16px;
        background-color: var(--border-color);
        margin-top: -8px;
      }
      a {
        padding: rem(10px) rem(15px);
        color: var(--primary);
      }
    }
  }
}

.team-slider,
.team-slider-two {
  .slick-list {
    margin: rem(-15px);
  }
  .team-slide {
    padding: rem(15px);
  }
}