.overview-section {
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 180px;
    background-color: #fff;
    @include media(767px) {
      height: 140px;
    }
  }
  .overview-section-line {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
    opacity: 0.35;
  }
}

.overview-content {
  @include media(991px) {
    text-align: center;
  }
  .section-title {
    color: #fff;
  }
  p {
    color: var(--light-200);
    margin-top: rem(20px);
  }
  .btn {
    margin-top: rem(35px);
    @include media(991px) {
      margin-top: rem(20px);
    }
  }
}

.overview-item-two {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: rem(18px) rem(20px) rem(28px) rem(20px);
  background-color: #1B1C28;
  @include border-radius(20px);
  @include media(575px) {
    justify-content: center;
  }
  .icon {
    width: rem(60px);
    height: rem(60px);
    background-color: var(--primary);
    @include border-radius(20px);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: rem(-15px);
  }
  .content {
    width: calc(100% - 60px);
    padding-inline-start: rem(35px);
    @include media(1199px) {
      padding-inline-start: rem(20px);
    }
    @include media(575px) {
      width: 100%;
      padding-inline-start: 0;
      margin-top: rem(25px);
      text-align: center;
    }
    .amount {
      font-size: rem(42px);
      font-weight: 700;
      color: var(--primary);
      font-family: var(--display-font);
      @include media(1199px) {
        font-size: rem(36px);
      }
      @include media(575px) {
        font-size: rem(32px);
      }
    }
    p {
      color: var(--light-200);
      margin-top: rem(8px);
    }
  }
}

.overview-wrapper {
  padding-top: rem(90px);
  position: relative;
  z-index: 2;
  margin-top: rem(70px);
  @include media(1199px) {
    margin-top: rem(30px);
  }
  @include media(767px) {
    padding-top: rem(30px);
  }
  &-inner {
    position: relative;
    padding: rem(60px);
    background-color: #fff;
    @include border-radius(20px);
    box-shadow: 0 0 60px rgba(#000, 0.05);
    z-index: 1;
    overflow: hidden;
    @include media(1199px) {
      padding: rem(40px);
    }
    @include media(767px) {
      text-align: center;
    }
  }
  .section-title {
    margin-bottom: rem(20px);
  }
  .about-bottom-users {
    margin-top: rem(25px);
    @include media(767px) {
      justify-content: center;
    }
  }
  .overview-wrapper-shape {
    position: absolute;
    bottom: 0;
    inset-inline-end: rem(25px);
    z-index: -1;
  }
  .overview-wrapper-man {
    position: absolute;
    inset-inline-end: 0;
    bottom: 0;
    z-index: 1;
    height: 100%;
    @include media(767px) {
      display: none;
    }
  }
}