.accordion-item {
  box-shadow: 0 4px 20px -1px rgba(#131022, 0.05);
  @include border-radius(30px !important);
  border: none;
  --bs-accordion-btn-icon-transform: rotate(90deg);
  & + & {
    margin-top: rem(30px);
  }
  .accordion-header {
    .accordion-button {
      @include border-radius(30px);
      font-size: rem(20px);
      padding: rem(15px) rem(30px);
      @include media(1199px) {
        font-size: rem(18px);
      }
      @include media(575px) {
        font-size: rem(16px);
      }
      &:not(.collapsed) {
        background-color: var(--primary);
        color: #fff;
        @include border-radius(30px 30px 0 0);
      }
      &:focus {
        box-shadow: none;
      }
      &::after {
        background-image: none;
        content: "\f054";
        font-family: "Font Awesome 6 Free";
        font-weight: 900;
      }
    }
  }
  .accordion-body {
    padding: rem(20px) rem(30px);
  }
}

.accordion {
  &.style-pill {
    .accordion-item {
      box-shadow: none;
      background-color: transparent;
      overflow: hidden;
      .accordion-header {
        .accordion-button {
          background-color: transparent;
          box-shadow: none;
          border: 1px solid var(--border-color);
          @include border-radius(30px);
          padding: rem(10px) rem(10px) rem(10px) rem(30px);
          @include media(1399px) {
            font-size: rem(18px);
          }
          @include media(575px) {
            font-size: rem(16px);
            padding-inline-start: rem(20px);
          }
          &::after {
            content: "\2b";
            width: rem(40px);
            height: rem(40px);
            background-color: #fff;
            @include border-radius(50%);
            display: inline-flex;
            justify-content: center;
            align-items: center;
            font-size: rem(18px);
            @include media(575px) {
              width: rem(30px);
              height: rem(30px);
              font-size: rem(16px);
            }
          }
          &:not(.collapsed) {
            color: var(--dark-900);
            background-color: #fff;
            @include border-radius(30px 30px 0 0);
            border-color: transparent;
            &::after {
              content: "\f068";
              transform: rotate(0deg);
              background-color: var(--primary);
              color: #fff;
            }
          }
        }
      }
      .accordion-collapse {
        background-color: #fff;
      }
      .accordion-body {
        padding-top: rem(10px);
      }
    }
  }
}