.testimonial-section-one {
  position: relative;
  .testimonial-section-one-line {
    position: absolute;
    bottom: 0;
    right: 0;
    max-inline-size: initial;
    max-block-size: initial;
  }

  .testimonial-section-one-thumb {
    position: relative;
    z-index: 1;
  }

  .section-top {
    @include media(991px) {
      text-align: center;
    }
    .section-title {
      @include media(991px) {
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
}

.testimonial-one {
  .content {
    padding: rem(40px) rem(40px) rem(70px) rem(40px);
    @include border-radius(20px);
    background-color: var(--dark-700);
    @include media(575px) {
      padding: rem(25px) rem(25px) rem(60px) rem(25px);
      text-align: center;
    }
    .icon {
      i {
        color: var(--primary);
        font-size: rem(60px);
        @include media(991px) {
          font-size: rem(48px);
        }
        @include media(575px) {
          font-size: rem(42px);
        }
      }
    }
    p {
      color: var(--light-200);
      font-size: rem(24px);
      @include media(1399px) {
        font-size: rem(22px);
      }
      @include media(991px) {
        font-size: rem(20px);
      }
      @include media(575px) {
        font-size: rem(18px);
      }
    }
  }
  .testimonial-client {
    padding-inline-start: rem(40px);
    margin-top: rem(-40px);
    @include media(575px) {
      padding-inline-start: 0;
      text-align: center;
    }
    .client-img {
      width: rem(73px);
      height: rem(73px);
      @include border-radius(50%);
      overflow: hidden;
      @include media(575px) {
        margin-left: auto;
        margin-right: auto;
      }
      img {
        width: 100%;
        height: 100%;
        @include object-fit;
      }
    }
  }
}

.testimonial-slider-one {
  z-index: 1;
  @include media(575px) {
    padding-bottom: rem(70px);
  }
  .slick-arrow {
    position: absolute;
    bottom: rem(10px);
    inset-inline-end: 0;
    width: rem(40px);
    height: rem(40px);
    color: #fff;
    border: 1px solid var(--primary);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @include border-radius(50%);
    @include transition(all 0.3s);
    z-index: 1;
    @include media(575px) {
      width: rem(35px);
      height: rem(35px);
    }
    &.prev {
      inset-inline-end: rem(55px);
      @include media(575px) {
        inset-inline-end: 50%;
      }
    }
    &.next {
      @include media(575px) {
        inset-inline-end: calc(50% - 40px);
      }
    }
    &:hover {
      color: #fff;
      background-color: var(--primary);
    }
  }
}

.testimonial-two-area {
  position: relative;
  padding: rem(80px) 0;
  @include media(991px) {
    padding: 0;
  }
  .testimonial-two-area-thumb {
    position: absolute;
    top: 0;
    right: 0;
    width: 57%;
    height: 100%;
    @include border-radius(20px);
    overflow: hidden;
    @include media(991px) {
      display: none;
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
  .testimonial-two-slider-area {
    padding: rem(60px);
    background-color: var(--dark-700);
    position: relative;
    z-index: 1;
    @include border-radius(20px);
    @include media(575px) {
      padding: rem(20px);
    }
    .testimonial-one {
      .content {
        padding: 0;
      }
      .testimonial-client {
        margin-top: 0;
        padding-inline-start: rem(0);
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-top: rem(35px);
        .client-img {
          width: rem(70px);
          height: rem(70px);
        }
        .client-details {
          width: calc(100% - 70px);
          padding-inline-start: rem(20px);
          @include media(575px) {
            width: 100%;
            padding-inline-start: 0;
            margin-top: rem(15px);
          }
        }
      }
    }
  }
}

.testimonial-section-three {
  position: relative;
  z-index: 1;
  .testimonial-section-three-el {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
    height: 100%;
    @include object-fit;
  }
}

.testimonial-three-slider-area {
  padding: rem(60px);
  background-color: #fff;
  position: relative;
  z-index: 1;
  @include border-radius(20px);
  @include media(1399px) {
    padding: rem(40px);
  }
  @include media(575px) {
    padding: rem(20px);
  }
  .testimonial-one {
    .content {
      padding: 0;
      background-color: #fff;
      p {
        color: var(--dark-800);
      }
    }
    .testimonial-client {
      margin-top: 0;
      padding-inline-start: rem(0);
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-top: rem(35px);
      .client-img {
        width: rem(70px);
        height: rem(70px);
      }
      .client-details {
        width: calc(100% - 70px);
        padding-inline-start: rem(20px);
        @include media(575px) {
          width: 100%;
          padding-inline-start: 0;
          margin-top: rem(15px);
        }
        p {
          margin-top: rem(5px);
        }
      }
    }
  }
  .testimonial-slider-one {
    .slick-arrow {
      color: var(--dark-900);
      border-color: var(--border-color);
      &:hover {
        border-color: var(--primary);
        color: #fff;
      }
    }
  }
}

.testimonial-section-four {
  .testimonial-one {
    .content {
      background-color: var(--light-100);
      p {
        color: var(--dark-800);
      }
    }
  }
  .testimonial-slider-one {
    .slick-arrow {
      border-color: var(--border-color);
      color: var(--dark-900);
      &:hover {
        border-color: var(--primary);
        color: #fff;
      }
    }
  }
}