h1 {
  font-size: rem(42px);
  @media (max-width: 1399px) {
    font-size: rem(36px);
  }
  @media (max-width: 1199px) {
    font-size: rem(32px);
  }
  @media (max-width: 575px) {
    font-size: rem(28px);
  }
}
h2 {
  font-size: rem(30px);
  @media (max-width: 991px) {
    font-size: rem(28px);
  }
  @media (max-width: 575px) {
    font-size: rem(24px);
  }
}

h3 {
  font-size: rem(20px);
}

h4 {
  font-size: rem(18px);
}

h5 {
  font-size: rem(16px);
}

h5 {
  font-size: rem(14px);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--body-font);
  color: var(--dark-900);
  font-weight: 500;
  margin: 0;
  line-height: 1.3;
  word-break: break-word;
}

h1 > a,
h2 > a,
h3 > a,
h4 > a,
h5 > a,
h6 > a {
  font-family: var(--body-font);
  color: var(--dark-900);
  font-weight: 500;
  @include transition(all 0.3s);
  line-height: 1.4;
  word-break: break-word;
}
p,
li,
span {
  margin: 0;
}
a {
  text-decoration: none;
  display: inline-block;
  font-family: var(--body-font);
  font-weight: 400;
}
a:hover {
  text-decoration: none;
}
strong {
  font-weight: 500;
}
.fs-18px {
  font-size: rem(18px) !important;
}
.fs-16px {
  font-size: rem(16px) !important;
}
.fs-15px {
  font-size: rem(15px) !important;
}
.fs-14px {
  font-size: rem(14px) !important;
}
.fs-12px {
  font-size: rem(12px) !important;
}
.h-font {
  font-family: var(--display-font) !important;
}
.p-font {
  font-family: var(--body-font) !important;
}

.max-18ch {
  max-width: 18ch;
}

.max-20ch {
  max-width: 20ch;
}

.max-55ch {
  max-width: 55ch;
}

.max-65ch {
  max-width: 65ch;
}