.footer-top {
  padding: rem(80px) 0;
  background-color: var(--dark-900);
  position: relative;
  overflow: hidden;
  z-index: 1;
  @include media(575px) {
    padding: rem(60px) 0;
  }
  .footer-logo {
    margin-bottom: rem(30px);
  }
  .footer-info-list {
    margin-top: rem(20px);
  }
  .footer-top-line {
    position: absolute;
    top: 0;
    left: 0;
    max-inline-size: initial;
    max-block-size: initial;
    opacity: 0.4;
    z-index: -1;
  }
}

.footer-info-list {
  li + li {
    margin-top: rem(20px);
  }
  li {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &:hover {
      .icon {
        background-color: var(--primary);
      }
    }
    .icon {
      width: rem(40px);
      height: rem(40px);
      background-color: rgba(#fff, 0.1);
      @include border-radius(5px);
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      @include transition(all 0.3s);
    }
    p {
      color: var(--light-200);
      width: calc(100% - 40px);
      padding-inline-start: rem(20px);
      a {
        color: var(--light-200);
      }
    }
  }
}

.dark-social-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: rem(10px);
  li {
    a {
      width: rem(30px);
      height: rem(30px);
      background-color: rgba(#fff, 0.1);
      color: #fff;
      @include border-radius(5px);
      @include transition(all 0.3s);
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: var(--primary);
      }
    }
  }
}

.footer-contact-list {
  li + li {
    margin-top: rem(20px);
  }
  li {
    display: flex;
    flex-wrap: wrap;
  }
  .icon {
    width: rem(30px);
    height: rem(30px);
    background-color: rgba(#fff, 0.1);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    @include border-radius(50%);
  }
  .caption {
    width: calc(100% - 30px);
    padding-inline-start: rem(10px);
    color: #fff;
    font-size: rem(18px);
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: rem(10px);
  }
  p:not(.caption) {
    width: 100%;
    color: #fff;
    margin-top: rem(5px);
  }
}

.footer-img-post-list {
  display: flex;
  flex-wrap: wrap;
  gap: rem(10px);
  li {
    width: rem(80px);
    height: rem(80px);
    overflow: hidden;
    @include border-radius(10px);
    a {
      display: block;
      &:hover {
        img {
          @include transform(scale(1.1, 1.1));
        }
      }
      img {
        width: 100%;
        height: 100%;
        @include object-fit;
        @include transition(all 0.3s);
      }
    }
  }
}

.footer-section-two {
  .footer-top {
    padding-top: 215px;
  }
}

.subscription-wrapper {
  position: relative;
  z-index: 2;
  padding: rem(70px) rem(60px);
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: -135px;
  @include media(1199px) {
    padding: rem(50px) rem(40px);
  }
  @include media(575px) {
    padding: rem(40px) rem(20px);
  }
  .section-top-title-two,
  .section-title {
    color: #fff;
  }
  .section-title {
    span {
      display: block;
      color: #fff;
      -webkit-text-fill-color: transparent;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: white;
    }
  }
  .subscribe-form {
    width: 500px;
    input {
      &:focus {
        border-color: #fff;
      }
    }
    button {
      background-color: #fff;
      color: var(--dark-900);
    }
  }
  &-bg-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include border-radius(20px);
    @include object-fit;
    z-index: -1;
  }
}

.footer-bottom {
  padding: rem(30px) 0;
  background-color: var(--dark-700);
}

.footer-logo {
  max-height: 95px;
}

.footer-title {
  color: #fff;
  margin-bottom: rem(40px);
  @include media(991px) {
    margin-bottom: rem(25px);
  }
}

.footer-menu {
  li + li {
    margin-top: rem(15px);
  }
  li {
    a {
      display: flex;
      align-items: center;
      gap: rem(8px);
      color: var(--light-200);
      @include transition(all 0.3s);
      &:hover {
        color: var(--primary);
      }
      i {
        color: #fff;
      }
    }
  }
}

.footer-post-list {
  .footer-single-post + .footer-single-post {
    margin-top: rem(25px);
  }
  .footer-single-post {
    display: flex;
    flex-wrap: wrap;
    .thumb {
      width: 80px;
      height: 80px;
      @include border-radius(5px);
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        @include object-fit;
      }
    }
    .content {
      width: calc(100% - 80px);
      padding-inline-start: rem(20px);
      .title {
        a {
          font-size: rem(16px);
          color: #fff;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
          &:hover {
            color: var(--primary);
          }
        }
      }
      span {
        color: var(--light-200);
        font-size: rem(14px);
      }
    }
  }
}

.footer-inline-menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: rem(-5px) rem(-15px);
  li {
    a {
      color: var(--light-200);
      padding: rem(5px) rem(15px);
      @include transition(all 0.3s);
      &:hover {
        color: var(--primary);
      }
    }
  }
}

.footer-section-three {
  position: relative;
  z-index: 1;
  background-color: var(--dark-900);
  &-el {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
  }
  .footer-top {
    background-color: transparent;
    position: static;
  }
  .footer-three-subscription-tile {
    font-size: rem(80px);
    font-weight: 700;
    color: #fff;
    font-family: var(--display-font);
    @include media(1399px) {
      font-size: rem(66px);
    }
    @include media(1199px) {
      font-size: rem(54px);
    }
    @include media(991px) {
      font-size: rem(42px);
    }
    @include media(575px) {
      font-size: rem(32px);
    }
    span {
      display: block;
      color: #fff;
      -webkit-text-fill-color: transparent;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: white;
    }
  }
  .subscribe-form {
    input {
      background-color: #0F1133;
      border-color: rgba(#fff, 0.1);
      height: 66px;
    }
    button {
      width: 46px;
      padding: 0;
      display: inline-flex;
      justify-content: center;
      align-items: center;
    }
  }
  .footer-bottom {
    background-color: transparent;
    &-area {
      background-color: var(--dark-700);
      @include border-radius(20px);
      padding: rem(30px);
    }
  }
}