.team-details-wrapper {
  background-color: var(--light-100);
  display: flex;
  flex-wrap: wrap;
  .thumb {
    width: 430px;
    @include media(991px) {
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
  .content {
    width: calc(100% - 430px);
    padding: rem(40px);
    @include media(991px) {
      width: 100%;
    }
    @include media(575px) {
      padding: rem(25px);
    }
    .designation {
      position: relative;
      padding-bottom: rem(5px);
      margin-top: rem(10px);
      &::after {
        position: absolute;
        content: '';
        bottom: 0;
        left: 0;
        width: 110px;
        height: 3px;
        background-color: var(--primary);
        @include border-radius(30px);
      }
    }
  }
}

.skill-bar p {
	margin-bottom: 10px;
}

.progressbar {
  position: relative;
  display: block;
  width: 100%;
  height: 8px;
  background-color: var(--light-200);
  @include border-radius(99px);
  .bar {
    position:absolute;
    width: 0px;
    height: 100%;
    top: 0;
    left: 0;
    background: var(--primary);
    overflow:hidden;
    @include border-radius(99px);
  }
  .label {
    position: absolute;
    top: -40px;
    left: 0;
    width: 30px;
    height: 30px;
    display: block;
    line-height: 34px;
    text-align: center;
    @include transform(translateX(-10px));
  }
}