.video-section-one {
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 180px;
    background-color: var(--light-100);
    z-index: -1;
    @include media(991px) {
      height: 120px;
    }
    @include media(767px) {
      height: 80px;
    }
  }
}

.video-wrapper {
  @include border-radius(20px);
  overflow: hidden;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    @include object-fit;
  }
  &-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: rem(70px);
    height: rem(70px);
    background-color: #fff;
    @include border-radius(50%);
    color: var(--primary);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(22px);
    @include media(575px) {
      width: rem(45px);
      height: rem(45px);
      font-size: rem(14px);
    }
  }
}