.ticker-section {
  padding: rem(60px) 0;
  background-color: var(--primary);
  overflow: hidden;
  @include media(991px) {
    padding: rem(30px) 0;
  }
}

.ticker-list {
  display: flex;
  align-items: center;
  gap: rem(40px);
  width: max-content;
  flex-wrap: nowrap;
  animation: scroll 40s forwards linear infinite;
  &:hover {
    animation-play-state: paused;
  }
  li {
    display: flex;
    align-items: center;
    gap: rem(40px);
    font-family: var(--display-font);
    font-size: rem(42px);
    font-weight: 700;
    color: #fff;
    @include media(1399px) {
      font-size: rem(36px);
    }
    @include media(991px) {
      font-size: rem(32px);
    }
    @include media(991px) {
      font-size: rem(26px);
    }
    img {
      max-height: rem(35px);
    }
  }
}

@include keyframes(scroll) {
  to {
    transform: translate(calc(-50% - 0.5rem));
  }
}