.faq-img {
  position: relative;
  padding-inline-end: rem(145px);
  z-index: 1;
  @include media(575px) {
    padding-inline-end: rem(45px);
  } 
  &::after {
    position: absolute;
    content: '';
    inset-inline-end: rem(120px);
    bottom: 0;
    width: rem(19px);
    height: rem(300px);
    background-color: var(--primary);
    z-index: -1;
  }
  .faq-main-img {
    @include border-radius(20px 5px 5px 20px);
  }
  .faq-float-img {
    position: absolute;
    top: rem(40px);
    inset-inline-end: 0;
    width: rem(295px);
    height: rem(400px);
    border: rem(14px) solid #fff;
    @include border-radius(20px);
    @include object-fit;
    @include media(575px) {
      width: rem(195px);
      height: rem(250px);
      border-width: rem(8px);
    }
  }
}

.faq-section-two {
  background-color: var(--light-100);
  padding-bottom: rem(60px);
}