.header-one,
.header-two,
.header-three {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
}

.site-header {
  &.site-header-fixed {
    position: fixed;
    box-shadow: 0 5px 5px rgba(#000, 0.1);
    .header-top {
      display: none;
    }
  }
}

.header-one {
  display: flex;
  flex-wrap: wrap;
  .header-right {
    flex-grow: 1;
  }
  .header-top {
    padding-inline: rem(60px);
    @include media(1399px) {
      padding-inline: rem(30px);
    }
  }
  .header-bottom {
    padding: rem(20px) rem(60px);
    @include media(1399px) {
      padding: rem(10px) rem(30px);
    }
    @include media(575px) {
      padding: rem(5px) rem(15px);
    }
  }
}

.header-two {
  .header-call-btn {
    margin-inline-start: rem(100px);
    @include media(1399px) {
      margin-inline-start: rem(40px);
    }
    @include media(1199px) {
      padding-inline-start: rem(60px);
    }
    @include media(575px) {
      padding-inline-start: 0;
      margin-inline-start: rem(15px);
    }
    i {
      @include media(1199px) {
        border-color: var(--border-color) !important;
        color: var(--dark-900) !important;
        width: rem(50px);
        height: rem(50px);
        margin-top: rem(-25px);
      }
      @include media(575px) {
        position: static;
        margin-top: 0;
      }
    }
    span {
      @include media(1199px) {
        color: var(--dark-900) !important;
      }
      @include media(575px) {
        display: none;
      }
    }
    
  }
  .header-bottom {
    background-color: var(--primary);
    position: relative;
    z-index: 1;
    @include media(1199px) {
      background-color: #fff;
    }
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 33vw;
      height: 100%;
      background-color: #fff;
      z-index: -1;
      clip-path: polygon(0 0, 100% 0%, 88% 100%, 0% 100%);
      @include media(1550px) {
        width: 30vw;
      }
      @include media(1399px) {
        width: 25vw;
      }
    }
  }
  .header-menu {
    > li {
      &.has-dropmenu {
        &::after {
          color: #fff;
        }
      }
      > a {
        color: #fff;
      }
    }
  }
}

.header-three {
  .header-top {
    background-color: var(--primary);
  }
  .header-bottom {
    background-color: var(--dark-900);
  }
  .header-info {
    li {
      color: #fff;
      i {
        color: #fff;
      }
    }
  }
  .header-social {
    li {
      span {
        color: #fff;
      }
      a {
        color: #fff;
        &:hover {
          color: #fff;
        }
      }
    }
  }
  .header-menu {
    >li {
      &.has-dropmenu {
        &::after {
          color: #fff;
        }
      }
      >a {
        color: #fff;
      }
    }
  }
  .header-bottom-action {
    margin-inline-start: rem(60px);
    @include media(1199px) {
      margin-inline-start: rem(40px);
    }
    @include media(767px) {
      margin-inline-start: rem(20px);
    }
    .header-search-btn {
      @include media(575px) {
        padding-inline-end: 0;
      }
    }
  }
  .header-search-btn,
  .btn-outline-primary,
  .mobile-menu-open-btn {
    color: #fff;
  }
}

.bg-header-logo {
  width: 315px;
  background-color: var(--primary);
  display: flex;
  justify-content: center;
  align-items: center;
  @include media(1399px) {
    width: 250px;
  }
  @include media(767px) {
    width: 200px;
  }
  @include media(575px) {
    width: 120px;
  }
  img {
    max-width: 280px;
    @include media(1399px) {
      max-width: 220px;
    }
    @include media(767px) {
      max-width: 170px;
    }
    @include media(575px) {
      max-width: 100px;
    }
  }
}

.header-top {
  background-color: #fff;
  padding: rem(13px) 0;
  display: flex;
  flex-wrap: wrap;
  gap: rem(10px);
  align-items: center;
  justify-content: space-between;
  @include media(1199px) {
    display: none;
  }
}
.header-info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: rem(30px);
  li {
    display: flex;
    align-items: center;
    gap: rem(10px);
    font-size: rem(14px);
    color: var(--dark-900);
    i {
      color: var(--primary);
    }
  }
}
.header-social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: rem(15px);
  li {
    font-size: rem(14px);
    color: var(--dark-900);
    a {
      &:hover {
        color: var(--primary);
      }
    }
  }
}

.header-bottom {
  background-color: var(--light-100);
  padding: rem(20px) 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: rem(30px);
  @include media(1199px) {
    padding: rem(10px) 0;
  }
}

.header-menu {
  display: flex;
  flex-wrap: wrap;
  margin: rem(-10px) rem(-20px);
  @include media(1199px) {
    display: none;
  }
  li {
    &.has-dropmenu {
      position: relative;
      &::after {
        position: absolute;
        content: "\f078";
        font-family: "Font Awesome 6 Free";
        font-weight: 900;
        top: 12px;
        right: 2px;
        font-size: 13px;
      }
      &:hover {
        .dropmenu {
          top: 100%;
          opacity: 1;
          visibility: visible;
        }
      }
    }
    a {
      color: var(--dark-900);
      font-weight: 500;
      padding: rem(10px) rem(20px);
    }
  }
  .dropmenu {
    position: absolute;
    top: 110%;
    left: 0;
    background-color: #fff;
    width: 220px;
    box-shadow: 0 5px 10px rgba(#000, 0.1);
    opacity: 0;
    visibility: hidden;
    @include transition(all 0.3s);
    li {
      border-bottom: 1px solid #e5e5e5;
      a {
        @include transition(all 0.3s);
        color: var(--dark-800);
        &:hover {
          color: var(--primary);
        }
      }
    }
  }
}

.header-bottom-action {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: rem(20px);
  @include media(1199px) {
    gap: rem(8px);
  }
  @include media(575px) {
    gap: rem(0px);
  }
  .btn {
    padding: rem(14px) rem(25px);
    @include media(575px) {
      display: none;
    }
  }
}

.header-search-btn {
  color: var(--dark-900);
  font-size: rem(18px);
  padding: rem(15px);
}

.header-call-btn {
  position: relative;
  display: inline-flex;
  flex-flow: column;
  justify-content: center;
  min-height: rem(60px);
  padding-inline-start: rem(75px);
  &:hover {
    i {
      background-color: var(--primary);
      color: #fff;
    }
  }
  i {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: rem(-30px);
    width: rem(60px);
    height: rem(60px);
    border: 1px solid var(--border-color);
    @include border-radius(50%);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(20px);
    transition: all 0.3s;
  }
  span {
    font-size: rem(14px);
    display: block;
    color: #fff;
    &.header-call-btn-number {
      font-size: rem(18px);
      font-weight: 500;
    }
  }
  &.style-white {
    &:hover {
      i {
        background-color: #fff;
        color: var(--primary);
      }
    }
    i {
      border-color: #fff;
      color: #fff;
    }
    span {
      color: #fff;
    }
  }
}

.mobile-menu-open-btn {
  color: var(--dark-900);
  font-weight: 500;
  padding: rem(10px) 0;
  display: none;
  @include media(1199px) {
    display: inline-flex;
    gap: 6px;
    align-items: center;
  }
  span {
    @include media(575px) {
      display: none;
    }
  }
}

.header-search-main {
  position: fixed;
  top: 50%;
  left: 50%;
  @include transform(translate(-50%, -50%));
  width: 100%;
  height: 0;
  background-color: var(--light-100);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  @include transition(all 0.5s);
  z-index: 9;
  &.active {
    height: 100vh;
    opacity: 1;
    visibility: visible;
  }
  .header-search-close-btn {
    position: absolute;
    top: 30px;
    right: 50px;
    @include transform(translateX(-50%));
    width: rem(35px);
    height: rem(35px);
    border: 1px solid var(--border-color);
    @include border-radius(50%);
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      color: var(--primary);
    }
    @include media(575px) {
      right: 30px;
    }
  }
  .header-search-form {
    display: flex;
    width: 500px;
    @include media(575px) {
      width: 300px;
    }
    input {
      height: 52px;
      border: 1px solid var(--border-color);
      border-right: none;
      flex-grow: 1;
      @include transition(all 0.3s);
      padding: rem(10px) rem(20px);
      &:focus {
        border-color: var(--primary);
      }
    }
    button {
      width: 50px;
      height: 52px;
      background-color: var(--primary);
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

.mobile-menu-sidebar {
  position: fixed;
  inset-block-start: 0;
  inset-inline-end: -350px;
  width: 320px;
  height: 100vh;
  background-color: var(--dark-900);
  display: flex;
  flex-flow: column;
  @include transition(all 0.3s);
  padding-top: 75px;
  padding-bottom: 50px;
  overflow-y: auto;
  z-index: 9;
  @include media(575px) {
    inset-inline-end: -105%;
    width: 100%;
  }
  &.active {
    inset-inline-end: 0;
  }
  .logo-area {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 75px;
    padding: rem(5px) rem(20px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    background-color: var(--primary);
    img {
      max-width: 180px;
    }
    .mobile-menu-close-btn {
      color: #fff;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid rgba(#ffff, 0.1);
      @include border-radius(50%);
    }
  }
  .mobile-menu-bottom {
    padding: rem(20px);
    margin-top: auto;
  }

  .header-info {
    display: flex;
    flex-flow: column;
    gap: rem(10px);
    align-items: flex-start;
    li {
      display: flex;
      align-items: center;
      gap: rem(10px);
      font-size: rem(14px);
      color: var(--light-100);
      i {
        color: var(--primary);
      }
    }
  }
  .header-social {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: rem(15px);
    margin-top: rem(15px);
    li {
      font-size: rem(14px);
      color: var(--light-100);
      a {
        &:hover {
          color: var(--primary);
        }
      }
    }
  }
}

.mobile-menu {
  li {
    border-bottom: 1px solid rgba(#fff, 0.1);
    a {
      padding: rem(8px) rem(20px);
      color: var(--light-100);
      position: relative;
      display: block;
      .dropmenu-arrow {
        position: absolute;
        width: 40px;
        height: 100%;
        inset-inline-end: 0;
        inset-block-start: 0;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-left: 1px solid rgba(#fff, 0.1);
      }
    }
    .dropmenu {
      display: none;
      background-color: rgba(#fff, 0.05);
      padding: rem(5px) rem(10px) rem(5px) rem(20px);
      li {
        border-bottom: none;
        a {
          font-size: rem(14px);
        }
      }
    }
  }
}