.project-quote {
  position: relative;
  padding: rem(30px);
  border: 1px solid var(--border-color);
  @include border-radius(5px);
  margin-top: rem(40px);
  display: flex;
  flex-wrap: wrap;
  @include media(575px) {
    padding: rem(20px);
  }
  &::after {
    position: absolute;
    content: "\f10e";
    bottom: 5px;
    inset-inline-end: 70px;
    font-family: "Font Awesome 6 Free";
    font-weight: 900;
    line-height: 1;
    font-size: rem(100px);
    opacity: 0.1;
  }
  .thumb {
    width: 90px;
    height: 90px;
    @include border-radius(50%);
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
  .content {
    width: calc(100% - 90px);
    padding-inline-start: rem(22px);
    @include media(575px) {
      width: 100%;
      padding-inline-start: 0;
      margin-top: rem(20px);
    }
  }
  .quote-name {
    position: relative;
    padding-inline-start: rem(25px);
    font-weight: 500;
    margin-top: rem(10px);
    &::before {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      width: rem(15px);
      height: 1px;
      background-color: var(--dark-900);
    }
  }
}

.project-details-sidebar {
  padding-block: rem(40px);
  padding-inline-end: rem(40px);
  background-color: var(--light-100);
  @include border-radius(5px);
  @include media(575px) {
    padding-block: rem(20px);
    padding-inline-end: rem(20px);
  }
  &-header {
    padding-inline: rem(40px);
    @include media(575px) {
      padding-inline: rem(20px);
    }
  }
}

.project-details-sidebar-list {
  li + li {
    margin-top: rem(20px);
  }
  li {
    padding: rem(10px) rem(40px);
    background-color: #fff;
    @include border-radius(0 5px 5px 0);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    @include media(575px) {
      padding: rem(10px) rem(20px);
    }
    span {
      &:first-child {
        font-size: rem(18px);
        font-weight: 500;
        color: var(--dark-900);
        width: 95px;
      }
      &:last-child {
        width: calc(100% - 95px);
      }
      &.rating {
        i {
          color: var(--primary);
          font-size: rem(14px);
        }
      }
    }
  }
}