.choose-section {
  position: relative;
  overflow: hidden;
  z-index: 1;
  &-line {
    position: absolute;
    left: 0;
    bottom: 0;
    opacity: 0.35;
    z-index: -1;
  }
}

.choose-item {
  padding: rem(20px) rem(30px);
  background-color: var(--dark-700);
  @include border-radius(20px);
  border-left: 1px solid var(--primary);
  &-title {
    color: #fff;
    margin-bottom: rem(12px);
  }
  p {
    color: #fff;
  }
}

.choose-thumb-one {
  padding-right: 30px;
  position: relative;
  img {
    @include border-radius(50px 0 0 50px);
  }
  &::after {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    width: 8px;
    height: 100%;
    background-color: var(--secondary);
    @include border-radius(50px);
  }
}

.choose-thumb-two {
  display: flex;
  flex-wrap: wrap;
  margin-top: rem(30px);
  .content {
    width: 190px;
    background-color: var(--primary);
    display: flex;
    flex-flow: column;
    justify-content: center;
    padding: rem(30px);
    @include border-radius(20px 0 0 20px);
    h3 {
      color: #fff;
      font-family: var(--display-font);
      font-weight: 700;
      font-size: rem(66px);
      margin-top: rem(-10px);
    }
    p {
      color: #fff;
      font-size: rem(18px);
      font-weight: 500;
    }
  }
  img {
    width: calc(100% - 190px);
    @include object-fit;
    @include border-radius(0 50px 50px 0);
  }
}