.about-section-one {
  position: relative;
  overflow: hidden;
  .about-section-one-el {
    position: absolute;
    bottom: 0;
    inset-inline-end: 0;
    @include media(1399px) {
      inset-inline-end: rem(-175px);
    }
  }
}

.about-section-two {
  position: relative;
  z-index: 1;
  .about-section-two-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
}

.about-two-item {
  padding-top: rem(35px);
  position: relative;
  .icon {
    position: absolute;
    top: 0;
    inset-inline-start: rem(30px);
    width: rem(60px);
    height: rem(60px);
    background-color: var(--primary);
    @include border-radius(20px);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .content {
    padding: rem(45px) rem(30px) rem(30px) rem(30px);
    background-color: #fff;
    @include border-radius(20px);
  }
}

.about-two-bottom {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: rem(50px);
  margin-top: rem(40px);
  @include media(575px) {
    gap: rem(20px);
  }
}

.about-bottom-users {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: rem(10px);
}

.about-section-three {
  position: relative;
  overflow: hidden;
  z-index: 1;
  .about-section-three-el {
    position: absolute;
    bottom: 0;
    inset-inline-end: 0;
    z-index: -1;
  }
}

.about-three-thumb {
  position: relative;
  z-index: 1;
  padding: 27px 0 27px 27px;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: calc(100% - 35px);
    height: 100%;
    border: 4.5px solid var(--primary);
    @include border-radius(65px 0 0 65px);
    z-index: -1;
  }
  img {
    @include border-radius(65px 0 0 65px);
    width: 100%;
    height: 100%;
    @include object-fit;
  }
  &-content {
    position: absolute;
    inset-inline-end: rem(35px);
    bottom: 62px;
    width: 120px;
    height: 120px;
    background-color: var(--dark-900);
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    @include border-radius(50%);
    span {
      font-size: rem(30px);
      font-weight: 700;
      color: #fff;
      display: block;
      line-height: 1.3;
      margin-bottom: rem(5px);
    }
    small {
      font-size: rem(18px);
      font-weight: 500;
      color: #fff;
      line-height: 1.3;
    }
  }
}

.about-three-content {
  .check-list {
    margin: 0;
  }
  .call-btn {
    margin-top: rem(30px);
    i {
      background-color: #fff;
      color: var(--dark-900);
    }
    strong {
      color: var(--dark-900);
    }
  }
}
