.blog-one {
  background-color: #fff;
  @include border-radius(20px);
  box-shadow: 0 15px 50px rgba(#000, 0.07); 
  height: 100%;
  .thumb {
    position: relative;
    height: 300px;
    @include border-radius(20px 20px 0 0);
    overflow: hidden;
    @include media(1399px) {
      height: rem(250px);
    }
    .blog-date {
      position: absolute;
      top: 0;
      inset-inline-end: 0;
      background-color: var(--light-100);
      color: var(--dark-900);
      font-size: rem(18px);
      text-transform: uppercase;
      width: 80px;
      height: 80px;
      @include border-radius(20px);
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: center;
      font-weight: 500;
      @include media(575px) {
        font-size: rem(16px);
        width: 70px;
        height: 70px;
      }
    }
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
  .content {
    padding: rem(30px) rem(40px) rem(40px) rem(40px);
    @include media(1399px) {
      padding: rem(30px);
    }
  }
  .blog-meta {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: rem(30px);
    margin-bottom: rem(20px);
    li {
      display: flex;
      align-items: center;
      gap: rem(8px);
      font-size: rem(14px);
      i {
        color: var(--dark-900);
      }
      span {
        margin-bottom: -1px;
      }
    }
  }
  .blog-title {
    margin-bottom: rem(20px);
  }
  .btn {
    margin-top: rem(40px);
  }
  &.style-two {
    .content {
      padding-top: rem(0);
    }
    .blog-meta {
      padding: rem(10px) rem(40px);
      margin-left: rem(-40px);
      margin-right: rem(-40px);
      background-color: var(--light-100);
      @include border-radius(0 0 20px 20px);
    }
  }
}


.blog-list-post {
  .thumb {
    position: relative;
    a {
      display: block;
    }
    img {
      @include border-radius(5px 5px 0 0);
      width: 100%;
    }
    .post-date {
      position: absolute;
      bottom: 0;
      inset-inline-end: 0;
      padding: rem(8px) rem(20px);
      background-color: var(--primary);
      color: #fff;
      font-size: rem(20px);
      font-weight: 500;
      @include media(575px) {
        font-size: rem(14px);
      }
    }
  }
  .blog-meta {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: rem(30px);
    margin-bottom: rem(30px);
    @include media(575px) {
      gap: rem(10px);
    }
  }
  .content {
    padding: rem(40px);
    background-color: var(--light-100);
    @include border-radius(0 0 5px 5px);
    @include media(575px) {
      padding: rem(25px);
    }
  }
  .title {
    margin-bottom: rem(20px);
    a {
      font-size: rem(42px);
      font-family: var(--display-font);
      font-weight: 700;
      @media (max-width: 1399px) {
        font-size: rem(36px);
      }
      @media (max-width: 1199px) {
        font-size: rem(32px);
      }
      @media (max-width: 575px) {
        font-size: rem(24px);
      }
    }
  }
  .btn {
    margin-top: rem(30px);
  }
}

.blog-list-post + .blog-list-post {
  margin-top: rem(60px);
  @include media(991px) {
    margin-top: rem(40px);
  }
}